/*Select lang style*/
.select {
  position: relative;
  width: max-content;
}
.select_lang-item__select .select__item {
  position: relative;
}
.select_lang-item__select .select__title {
  color: #000001;
  background-color: transparent;
  cursor: pointer;
}
.select_lang-item__select .select__value {
  display: flex;
  font-family: "FuturaPT-Medium", serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 120%;
  align-items: center;
  justify-content: space-between;
  flex-direction: row-reverse;
  width: max-content;
  padding-top: 8px;
  padding-bottom: 8px;
  padding-left: 13px;
  padding-right: 13px;
  color: #000000;
}
@media (max-width: 1440px) {
  .select_lang-item__select .select__value {
    font-size: calc(14px + 2 * ((100vw - 320px) / 1120));
  }
}
@media (max-width: 1440px) {
  .select_lang-item__select .select__value {
    padding-left: calc(12px + 1 * ((100vw - 320px) / 1120));
  }
}
@media (max-width: 1440px) {
  .select_lang-item__select .select__value {
    padding-right: calc(12px + 1 * ((100vw - 320px) / 1120));
  }
}
.select_lang-item__select .select__value span {
  height: 1em;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
}
.select_lang-item__select .select__value::before {
  content: "";
  margin-left: 4px;
  display: inline-block;
  transition: all 0.3s ease 0s;
  width: 8px;
  height: 10px;
  background: url("./assets/img/strelka.png") center/100% no-repeat;
}
.select_lang-item__select .select__options {
  color: #000000;
  display: none;
  position: absolute;
  top: 20px;
  border-radius: 22px;
  min-width: 100%;
  left: 0;
  background-color: transparent;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  font-family: "FuturaPT-Medium";
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 120%;
  align-items: center;
  transition: all 0.3s ease 0s;
}
@media (max-width: 1440px) {
  .select_lang-item__select .select__options {
    font-size: calc(14px + 2 * ((100vw - 320px) / 1120));
  }
}
.select_lang-item__select .select__option {
  cursor: pointer;
  padding-top: 7px;
  padding-bottom: 7px;
  padding-left: 13px;
  padding-right: 13px;
  margin: 0px 0px 0px 0px;
}
@media (max-width: 1440px) {
  .select_lang-item__select .select__option {
    padding-left: calc(12px + 1 * ((100vw - 320px) / 1120));
  }
}
@media (max-width: 1440px) {
  .select_lang-item__select .select__option {
    padding-right: calc(12px + 1 * ((100vw - 320px) / 1120));
  }
}
.select_lang-item__select .select._active {
  z-index: 5;
}
.select_lang-item__select._active .select__value::before {
  transform: rotate(-180deg);
}
.select_lang-item__select._active .select__title {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  border-bottom: none;
}
